import React, { FC, useState, useEffect } from "react"
import { Helmet } from "react-helmet"
import { analyticsTrack } from "../utils"
import {
  Layout,
  LoadingScreen,
  Participant,
  ParticipantForm,
  Popup,
} from "../components"
import easterAdvertise from "../assets/easter-advertise.png"

interface State {
  loading: boolean
  participants?: any
  group?: {
    name: string
    comments: string
  }
}

const Content = ({ participants, group, showFriendOnClick }) => {
  const [secretFriend, setSecretFriend] = useState([])
  const [
    shuffledparticipantsWithoutSecretFriend,
    setShuffledparticipantsWithoutSecretFriend,
  ] = useState([])

  useEffect(() => {
    const shuffleArray = (array: never[]) => {
      const arrayCopy = [...array]
      for (let i = arrayCopy.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
        const temp = arrayCopy[i]
        arrayCopy[i] = arrayCopy[j]
        arrayCopy[j] = temp
      }
      return arrayCopy
    }

    setShuffledparticipantsWithoutSecretFriend(
      shuffleArray(
        participants.others.filter(
          (participant: { name: any }) =>
            participant.name.trim() !== participants.current.friend.trim()
        )
      )
    )

    setSecretFriend(
      participants.others.find(
        (participant: { name: any }) =>
          participant.name.trim() === participants.current.friend.trim()
      )
    )
  }, [])

  const goToAdvertisePage = (link, category, action, label) => {
    analyticsTrack(category, action, label)
    window.open(link, "_blank", 'rel="noreferrer"')
  }

  return (
    <div className="page-container pt-4">
      <section className="row">
        <button
          onClick={() =>
            goToAdvertisePage(
              "https://www.meliuz.com.br/premium-link/538?utm_source=papelzinho&utm_medium=partner&utm_campaign=campanha100&utm_content=papelzinho-pascoa-2980",
              "banner-meliuz",
              "clique",
              "acao-pascoa-meliuz"
            )
          }
          className="mb-4 group__button"
        >
          <img
            className="group__banner"
            src={easterAdvertise}
            alt="Pascóa com Méliuz"
          ></img>
        </button>
      </section>

      <section className="mb-4">
        <h2 className="group__name font-weight-bolder">{group.name}</h2>
        <span className="group__participants">
          {participants.others.length + 1} participantes
        </span>
        <p className="group__description">{group.comments}</p>

        <section className="mb-4">
          <button className="show-friend-btn" onClick={showFriendOnClick}>
            Revelar meu amigo secreto
          </button>
        </section>
      </section>

      <section className="mb-5">
        <div className="group__friend row">
          <Participant participant={secretFriend} isSecretFriend={true} />
          <ParticipantForm participant={participants.current} />
        </div>
        <p
          className="font-weight-bolder"
          style={{ fontSize: "1.8rem", marginBottom: "32px" }}
        >
          Outros Participantes
        </p>
        <div className="row">
          {shuffledparticipantsWithoutSecretFriend.map((participant, index) => (
            <Participant key={index} participant={participant} />
          ))}
        </div>
      </section>
    </div>
  )
}

const SorteioPage: FC = () => {
  const initialState: State = {
    loading: true,
  }
  const [state, setState] = useState(initialState)

  const [showPopup, setShowPopup] = useState(false)

  const showFriend: () => void = () => {
    setShowPopup(true)
  }

  const hideFriend = () => {
    analyticsTrack("cancel-button", "clique", "Campanha Méliuz")
    setShowPopup(false)
  }

  useEffect(() => {
    const query = new URLSearchParams(window.location.search)
    const groupId = query.get("g")
    const personId = query.get("p")

    fetch(`${process.env.GATSBY_API_HOST}/participant/${groupId}/${personId}`)
      .then((response) => response.json())
      .then((data) => {
        if (data.message === "Group not found") {
          alert(
            "Aparentemente o seu grupo de amigo secreto foi deletado pelo organizador no app. Caso esta mensagem esteja errada e seu grupo não tenha sido excluído, entre em contato com o suporte através do meajuda@papelzinho.com.br ou pelo chat na nossa página do facebook."
          )
          return
        } else if (data.message === "") {
          alert(
            "Participante não encontrado. Entre em contato com o suporte através do meajuda@papelzinho.com.br ou pelo chat na nossa página do facebook."
          )
          return
        }

        const participants = data.participants.reduce(
          (acc: any, participant: { current: any }) => {
            const newMap = acc
            if (participant.current) {
              newMap["current"] = {
                ...participant,
                groupId,
              }
            } else if (newMap["others"]) {
              newMap["others"].push(participant)
            } else {
              newMap["others"] = [participant]
            }
            return newMap
          },
          {}
        )

        setState({
          loading: false,
          participants,
          group: { name: data.name, comments: data.comments },
        })
        setShowPopup(true)
      })
      .catch(() => {
        alert(
          "Sistema fora do ar. Estamos trabalhando para corrigir o problema o mais rápido possível. Tente acessar novamente mais tarde."
        )
      })
  }, [false])

  return (
    <Layout>
      <Helmet>
        <title>Sorteio | Papelzinho ™</title>
      </Helmet>

      {state.loading ? (
        <LoadingScreen />
      ) : (
        <Content
          showFriendOnClick={showFriend}
          group={state.group}
          participants={state.participants}
        />
      )}

      {showPopup && (
        <Popup
          message={`"${state.participants.current.friend}"`}
          action2={hideFriend}
        />
      )}
    </Layout>
  )
}

export default SorteioPage
